.transition, .blogIndex__relatedProducts__card__img, .blogpage__card__img, .footer a, .footer ul li a, header .navbar--desktop li a, header, .btn, .typography a:not(.btn) {
  transition: all 0.4s linear;
}

.transition- {
  transition: all 0.3s linear;
}

[data-fadein] {
  @apply opacity-0 duration-[500ms];
  transform: translateY(30px);
}
[data-fadein].active {
  @apply opacity-100;
  transform: translateY(0);
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
    transform: translateY(-20px);
    /* Start position: 20px up */
  }
  100% {
    opacity: 1;
    transform: translateY(0);
    /* End position: No translation */
  }
}
@keyframes fadeOutAnimation {
  0% {
    opacity: 1;
    transform: translateY(0);
    /* Start position: No translation */
  }
  100% {
    opacity: 0;
    transform: translateY(-20px);
    /* End position: 20px up */
  }
}
.fadeInSlide {
  animation: fadeInAnimation 0.3s ease forwards;
}

.fadeOutSlide {
  padding: 0;
  height: 0;
  animation: fadeOutAnimation 0.2s ease forwards;
}

[data-fadein] {
  @apply opacity-0 duration-[500ms];
  transform: translateY(30px);
}
[data-fadein].active {
  @apply opacity-100;
  transform: translateY(0);
}

